.title {
    font-size: 23px;
    font-weight: 700;
    margin: 20px 0 10px 0;
}

.daily-item {
    background-color: #f5f5f5;
    border-radius: 15px;
    height: 40px;
    margin: 5px;
    display: flex;
    align-items: center;
    cursor: pointer;
    font-size: 14px;
    padding: 5px 20px;
}

.icon-small {
    max-width: 40px;
}

.day {
    color: #212121;
    flex: 1 1;
    font-weight: 600;
    margin-left: 15px;
}

.description {
    flex: 1 1;
    text-align: left;
    margin-right: 15px;
}

.min-max{
    color: #757575;
}

.daily-details-grid{
    display: grid;
    grid-column-gap: 15px;
    row-gap: 0;
    column-gap: 15px;
    grid-template-columns: auto auto;
    padding: 5px 15px;
}

.daily-details-grid-item{
    display: flex;
    align-items: center;
    height: 30px;
    justify-content: space-between;
}

.daily-details-grid-item label:first-child{
    color: #757575;
}

.daily-details-grid-item label:last-child{
    color: #212121;
}